
.icons{
    
    font-size: 32px;
    height : 32px ;
    width : 32px ;
    color: #ddd;
    cursor: pointer;
  
}

.icons:hover {
    color: red; 
}
.icon-con {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    padding: 7px;
    background-color: #fff;
    margin: 7px;
}

.links{
   
    text-decoration: none;
    font-size: 24px;
    color: #002f34;
    font-weight: bold;
}

.price-text{
    color: '#002f34';
}